body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Home-segment{
    padding: 1em 0em;
    min-height: 500px;
}

h1.ui.header.Home-h1 {
    font-size: 4em;
    font-weight: normal;
    margin-bottom: 0;
    margin-top: 2em;
}

h2.ui.header.Home-h2 {
    font-size: 1.7em;
    font-weight: normal;
    margin-top: 1.5em;
}

h3.ui.header.Home-h3 {
    font-size: 2em;
}

.Home-segment p {
    font-size: 1.33em;
}
.Project-segment {
    padding: 5em 0em;
}

.About-intro > p {
    font-size: 1.33em;
}

.About-segment {
    padding: 5em 0em;
}
.Contact-segment {
    padding: 5em 0em;
}

.Contact-contactinfo > p {
    font-size: 1.33em;
}

.Contact-contactinfo > blockquote {
    font-size: 1.33em;
    font-style: italic;
    border-left: #4a4a4a .4em solid;
}

.Contact-contactinfo > blockquote > p {
    margin: .8em .8em;
}
